













import Vue from 'vue'
import * as R from "ramda";

import GameState from '@/core/GameState';
import Player from '@/core/player/Player';

export default Vue.extend({
    name: "GameStatus",
    props: {
        gameState: {
            required: true,
            type: GameState
        }
    },
    computed: {
        players() : Player[] {
            return R.values(this.gameState.players);
        }
    }
})
